*, :before, :after {
  box-sizing: border-box;
}

body {
  color: var(--color-text);
  background-color: var(--color-bg);
  margin: 0;
  padding: 3rem;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
}

img {
  max-width: 100%;
  display: block;
}

:root {
  --color-bg: #121226;
  --color-header-bg: #202046;
  --color-text: #ededed;
}

.wrapper {
  width: min(100% - 40px, 68.75rem);
  margin-inline: auto;
}

.wrapper.has-padding {
  padding-block: 40px;
}

@media (width >= 43.75rem) {
  .wrapper.has-padding {
    padding-block: 60px;
  }
}

.rounded {
  border-radius: 16px;
  overflow: hidden;
}

.nav {
  background-color: var(--color-header-bg);
  flex-direction: row;
  justify-content: flex-end;
  gap: 2rem;
  max-height: 2rem;
  display: flex;
}

.nav-list {
  color: var(--color-text);
  font-size: 2rem;
  font-weight: 500;
  list-style: none;
}
